* {
  box-sizing: border-box; }

html, body {
  height: 100%;
  font-size: 18px; }

body {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  -webkit-print-color-adjust: exact;
  -webkit-font-smoothing: antialiased; }

.c-image-select {
  width: 100%;
  height: 36px;
  position: relative;
  border: #a2afb9 solid 1px;
  cursor: pointer;
  display: flex;
  align-items: flex-start; }
  .c-image-select:after {
    content: '\25BC';
    flex: 0 0 20px;
    vertical-align: middle;
    align-self: center; }
  .c-image-select__selected-image {
    background: no-repeat 50% 50%;
    background-size: auto 90%;
    flex: 1 1 auto; }
  .c-image-select__list {
    position: absolute;
    width: 100%;
    background: #F2F4F5;
    border: #ddd solid 1px;
    top: 100%;
    left: 0;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    flex-wrap: wrap;
    z-index: 9999; }
  .c-image-select__list-item {
    flex: 0 1 80px;
    padding: 10px;
    cursor: pointer; }
    .c-image-select__list-item:hover {
      background: #ddd; }
  .c-image-select__image {
    width: 100%;
    height: 0;
    padding-top: 100%;
    background: no-repeat 50% 50%;
    background-size: contain; }

.c-devices__list {
  list-style: none;
  padding: 0;
  margin: 0; }

.c-devices__list-item {
  display: flex;
  margin-bottom: 5px; }

.c-devices__image-select {
  flex: 0 0 80px;
  margin-right: 5px; }

.c-devices__name {
  flex: 1 1 auto;
  margin: 0; }

.c-devices__header {
  display: flex;
  align-items: center;
  margin-bottom: 5px; }

.c-devices__label {
  flex: 1 1 auto; }

.c-devices__add, .c-devices__delete {
  flex: 0 0 32px;
  text-align: center;
  font-size: 23px;
  cursor: pointer; }
  .c-devices__add:hover, .c-devices__delete:hover {
    background: #D4DADE; }

.c-main-heading {
  flex: 0 1 auto; }

.c-main {
  flex: 1 1 auto;
  display: flex; }

.c-configuration {
  font-size: 16px;
  flex: 0 0 350px;
  flex-direction: column;
  padding: 15px;
  align-items: stretch;
  overflow: scroll; }
  .c-configuration__label, .c-configuration__input {
    display: block;
    flex: 1 1 auto;
    width: 100%;
    margin: 0; }
  .c-configuration__label {
    font-size: 16px;
    font-family: 'Source Sans Pro';
    font-weight: 200;
    color: #265b80; }
  .c-configuration__label--big {
    font-family: 'Oswald';
    font-size: 26px; }
  .c-configuration__label--radio {
    width: auto;
    display: inline-block;
    margin: 0 10px 0 5px; }
  .c-configuration__input {
    margin-bottom: 15px; }
  .c-configuration__input--radio {
    width: auto;
    display: inline-block; }
  .c-configuration__fieldset {
    margin-top: 10px; }
  .c-configuration__face {
