.c-preview {
  flex: 1 1 auto;
  display: flex;
  flex-direction: row;
  align-items: stretch; }
  .c-preview__main {
    flex: 2 1 auto; }
  .c-preview__name {
    background: #5b7183 !important;
    margin: 0;
    color: #fff !important;
    font-family: 'Source Sans Pro';
    font-weight: 200;
    font-size: 2rem;
    padding: 1.25rem;
    text-transform: uppercase;
    font-weight: 200; }
  .c-preview__question-section {
